export const JSONP = {
  data: {
    name: 'JSONP(JSON)',
    params: 'XMLHttpRequest',
    paramsDes: 'XMLHttpRequest,',
    return: 'new Promise()',
    attention: 'script scriptJSONPget',
    description: 'XMLHttpRequestscript src  GET  JSONP ',
  },
  example: `const jsonp =()=>{
    handwriting context;
  }
  jsonp({
  url: 'http://localhost:3000',
  params: { 
    a: 1,
    b: 2
  }
}).then(data => {
  // 
  console.log(data); // 
})
  `,
  handwriting: `const jsonp=({url,params,callback})=>{
    const getUrl =()=>{
      let dataParams = ''
      for(let i of params){
        dataParams+ = i
      }
      dataParams+='callback='+callback
      return \${url}?\${dataParams}
    }
    return new Promise((resolve,reject)=>{
      let scriptEle = document.createElement('script')
      scriptEle.src = getUrl()
      document.body.appendChild(scriptEle);
      //
      window[callback] = (data)=>{
        resolve(data)
        //scriptscript
        document.body.removeChild(scriptEle);
      }
  })
  }`,
  image: false,
}
